<script>
	import { afterUpdate, onMount } from 'svelte';
	import katex from 'katex';
	import html2canvas from 'html2canvas';
  
	let latexInput = "x=\\frac{-b\\pm\\sqrt{b^2-4ac}}{2a}";
	let textArea;
	let outputElement;
	let latexLogo;
	let copyImageButton;
	let copyLatexButton;
	let scale = 5; 
	let transparentBackground = false;

	onMount(() => {
	  renderLatex();
	  renderLogo();
	});

	afterUpdate(() => {
		adjustHeight();
	});

	function renderLogo() {
		katex.render("\\LaTeX", latexLogo, {
			throwOnError: false
		});
	}

	function adjustHeight() {
		if (textArea) {
			// This ensures the textarea has a base size before adjusting
			textArea.style.height = 'auto'; 
			// Set the height to scrollHeight to fit the content
			textArea.style.height = `${textArea.scrollHeight}px`;
		}
	}

	// Call adjustHeight whenever latexInput changes
	$: latexInput, renderLatex();

	function renderLatex() {
	    if (outputElement) {
			katex.render(latexInput, outputElement, {
				displayMode: true,
				throwOnError: false
			});
	    }
		adjustHeight();
	}

	async function copyImage()  {
		const canvas = await html2canvas(outputElement, {
			scale: scale,
			backgroundColor: transparentBackground ? 'transparent' : undefined 
		});
		canvas.toBlob((blob) => {
			navigator.clipboard.write([
				new ClipboardItem({
					'image/png': blob
				})
			]);
		});	
		copyImageButton.textContent = "Copied!";
		setTimeout(() => {
			copyImageButton.textContent = "Copy Image";
		}, 1000);
	}

	async function copyLatex() {
		navigator.clipboard.writeText(latexInput);
		copyLatexButton.textContent = "Copied!";
		setTimeout(() => {
			copyLatexButton.textContent = "Copy LaTeX";
		}, 1000);
	}

</script>
 
<style>
.auto-height-textarea {
	min-height: 10px;
	height: 10px;
	overflow-y: hidden; /* Prevent scroll bar flash */
	resize: none; /* Optional: prevent manual resizing */
}

.textarea.latex-input {
    background-color: darkslategrey;
    color: white;
    font-family: monospace;
}
.latex-output {
	padding-left: 10px;
	padding-right: 10px;
	display: inline-block;
    font-size: 3em;
}
.slider {
	width: 200px;
}
.copy-image {
    display: flex;
    align-items: center; /* Align the items vertically in the center */
	gap: 20px;
}

.copy-image button {
	width: 150px;
}

.scale-range {
    display: flex;
    flex-direction: column; /* Stack the scale text and slider vertically */
}

.slider {
    margin-top: 5px; /* Add a little space above the slider */
}

</style>

<section class="section">
	<div class="container content">
		<h1><span class="latex-logo" bind:this={latexLogo}></span><span style="margin-left: 10px;">Previewer</span></h1>
		<textarea class="latex-input textarea auto-height-textarea" rows="1" bind:this={textArea} bind:value={latexInput}></textarea>
		<div class="latex-output" bind:this={outputElement}></div>
		<div class="copy-image">
			<button class="copy-image button" on:click={copyImage} bind:this={copyImageButton}>Copy Image</button>
			<div class="scale-range">
				<div class="scale-text">
					Scale: {scale}
				</div>
				<input type="range" min="0.5" max="10" step="0.01" bind:value={scale} class="slider">
			</div>
			<label>
				<input type="checkbox" bind:checked={transparentBackground}> Transparent Background
			</label>
		</div>
	</div>
</section>